<template>
  <div id="footer" class="container-fluid">
    <div class="footer-content">
      <div class="row">
        <div class="col-md-4 col-sm-12 footer-brand">
          <div class="logo">
            <img src="@/assets/img/zhimingzhelogo.png" alt="logo">
          </div>
          <p class="title"></p>
          <p class="slogan"></p>
        </div>
        
        <div class="col-md-4 col-sm-12 footer-contact">
          <h4></h4>
          <div class="contact-container">
            <div class="contact-group">
              <h5></h5>
              <p><i class="glyphicon glyphicon-map-marker"></i> 2077</p>
              <p><i class="glyphicon glyphicon-phone"></i> +852 2814 1083</p>
              <p><i class="glyphicon glyphicon-envelope"></i> contact@zhimingzhe.com</p>
            </div>
            <div class="contact-group">
              <h5></h5>
              <p><i class="glyphicon glyphicon-map-marker"></i> 20703</p>
              <p><i class="glyphicon glyphicon-phone"></i> +86 18950152705</p>
              <p><i class="glyphicon glyphicon-envelope"></i> contact@zhimingzhe.com</p>
            </div>
          </div>
        </div>
        
        <div class="col-md-4 col-sm-12 footer-links">
          <h4></h4>
          <ul>
            <li><a href="/"></a></li>
            <li><a href="/service"></a></li>
            <li><a href="/companyintroduction"></a></li>
            <li><a href="/contactus"></a></li>
          </ul>
          <!--  -->
          <div class="social-links">
            <div class="social-item">
              <a href="#" class="social-link" title="" @click.prevent="showQRCode('weixin')">
                <img src="@/assets/img/weixin.png" alt="" class="social-icon">
              </a>
              <div class="qr-popup" v-if="activeQR === 'weixin'">
                <img src="@/assets/img/wechat.png" alt="" class="qr-code">
              </div>
            </div>
            <div class="social-item">
              <a href="#" class="social-link" title="" @click.prevent="showQRCode('weibo')">
                <img src="@/assets/img/weibo.png" alt="" class="social-icon">
              </a>
              <div class="qr-popup" v-if="activeQR === 'weibo'">
                <img src="@/assets/img/wechat.png" alt="" class="qr-code">
              </div>
            </div>
            <div class="social-item">
              <a href="#" class="social-link" title="" @click.prevent="showQRCode('xiaohongshu')">
                <img src="@/assets/img/rednote.png" alt="" class="social-icon">
              </a>
              <div class="qr-popup" v-if="activeQR === 'xiaohongshu'">
                <img src="@/assets/img/wechat.png" alt="" class="qr-code">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="footer-bottom">
      <p class="copy">Copyright &copy; 2024 - {{ new Date().getFullYear() }}  </p>
    </div>
    <!-- QR -->
    <div class="qr-modal" v-if="activeQR">
      <div class="qr-modal-content">
        <div class="qr-modal-header">
          <h4>{{ getQRTitle }}</h4>
          <button class="qr-close-btn" @click="closeQRCode">&times;</button>
        </div>
        <div class="qr-modal-body">
          <img :src="getQRImage" :alt="getQRTitle + ''" class="qr-code-img">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Footer",
  data() {
    return {
      activeQR: null,
      qrCodeData: {
        weixin: {
          title: '',
          image: require('@/assets/img/wechat.png')
        },
        weibo: {
          title: '',
          image: require('@/assets/img/wechat.png') // 
        },
        xiaohongshu: {
          title: '',
          image: require('@/assets/img/wechat.png') // 
        }
      }
    };
  },
  computed: {
    getQRTitle() {
      return this.activeQR ? this.qrCodeData[this.activeQR].title : '';
    },
    getQRImage() {
      return this.activeQR ? this.qrCodeData[this.activeQR].image : '';
    }
  },
  methods: {
    showQRCode(type) {
      this.activeQR = type;
      // body
      document.body.classList.add('modal-open');
    },
    closeQRCode() {
      this.activeQR = null;
      // body
      document.body.classList.remove('modal-open');
    },
    handleClickOutside(e) {
      if (!e.target.closest('.social-item') && !e.target.closest('.qr-modal-content')) {
        this.closeQRCode();
      }
    }
  },
  mounted() {
    document.addEventListener('click', this.handleClickOutside);
  },
  beforeDestroy() {
    document.removeEventListener('click', this.handleClickOutside);
  }
};
</script>

<style scoped>
#footer {
  width: 100%;
  color: #fff;
  background: #2c3e50;
  overflow: hidden;
  padding: 0;
  margin: 0;
}

.footer-content {
  padding: 60px 10% 40px;
}

.footer-brand .logo {
  width: 120px;
  height: 60px;
  margin-bottom: 20px;
}

.footer-brand .logo img {
  max-width: 100%;
  height: auto;
}

.footer-brand .title {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 10px;
}

.footer-brand .slogan {
  color: #bdc3c7;
  font-size: 14px;
}

.footer-contact h4,
.footer-links h4 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 20px;
  position: relative;
  padding-bottom: 10px;
}

.footer-contact h4:after,
.footer-links h4:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 50px;
  height: 2px;
  background: #3498db;
}

.contact-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}

.contact-group {
  flex: 0 0 48%;
  margin-bottom: 15px;
}

.contact-group h5 {
  font-size: 16px;
  font-weight: 500;
  color: #fff;
  margin-bottom: 8px;
}

.footer-contact p {
  color: #bdc3c7;
  margin-bottom: 10px;
  font-size: 14px;
}

.footer-contact i {
  margin-right: 10px;
  color: #3498db;
}

.footer-links ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links ul li {
  margin-bottom: 10px;
}

.footer-links ul li a {
  color: #bdc3c7;
  text-decoration: none;
  transition: all 0.3s ease;
}

.footer-links ul li a:hover {
  color: #3498db;
  padding-left: 5px;
}

/*  */
.social-links {
  margin-top: 20px;
  display: flex;
}

.social-item {
  position: relative;
  margin-right: 15px;
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  transition: all 0.3s ease;
}

.social-link:hover {
  background: #3498db;
  transform: translateY(-3px);
}

.social-icon {
  width: 20px;
  height: 20px;
  object-fit: contain;
}

/*  */
.qr-popup {
  display: none;
}

/*  */
.qr-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.qr-modal-content {
  background-color: #fff;
  border-radius: 8px;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  position: relative;
}

.qr-modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  border-bottom: 1px solid #eee;
  background-color: #f8f8f8;
}

.qr-modal-header h4 {
  margin: 0;
  font-size: 18px;
  color: #333;
  font-weight: 500;
}

.qr-close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 24px;
  color: #666;
  cursor: pointer;
  transition: color 0.3s;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.qr-close-btn:hover {
  color: #e74c3c;
  background-color: rgba(0, 0, 0, 0.05);
}

.qr-modal-body {
  padding: 30px 20px;
  text-align: center;
  background-color: #fff;
}

.qr-code-img {
  width: 220px;
  height: 220px;
  object-fit: contain;
  display: block;
  margin: 0 auto;
}

/*  */
:global(.modal-open) {
  overflow: hidden;
}

/*  */
@media (max-width: 480px) {
  .qr-modal-content {
    width: 95%;
    max-width: 320px;
  }
  
  .qr-code-img {
    width: 200px;
    height: 200px;
  }
}

/*  -  */
@media (max-width: 767px) {
  #footer {
    background: linear-gradient(135deg, #2c2c2c 0%, #8b8680 100%);
    position: relative;
  }
  
  #footer::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: 
      radial-gradient(circle at 20% 30%, rgba(212, 175, 55, 0.05) 0%, transparent 50%),
      radial-gradient(circle at 80% 70%, rgba(200, 16, 46, 0.05) 0%, transparent 50%);
    pointer-events: none;
  }
  
  .footer-content {
    padding: 30px 15px 20px;
    position: relative;
    z-index: 2;
  }
  
  .row {
    flex-direction: column;
    gap: 25px;
  }
  
  .footer-brand {
    text-align: center;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(212, 175, 55, 0.3);
  }
  
  .footer-brand .logo {
    width: 80px;
    height: 40px;
    margin: 0 auto 15px;
  }
  
  .footer-brand .title {
    font-size: 18px;
    margin-bottom: 8px;
    color: #faf9f7;
    font-family: 'Microsoft YaHei', '', sans-serif;
    font-weight: 700;
  }
  
  .footer-brand .slogan {
    font-size: 13px;
    color: rgba(212, 175, 55, 0.8);
    font-family: 'Microsoft YaHei', '', sans-serif;
  }
  
  .footer-contact, .footer-links {
    text-align: center;
  }
  
  .footer-contact h4, .footer-links h4 {
    font-size: 16px;
    margin-bottom: 15px;
    color: #d4af37;
    font-family: 'Microsoft YaHei', '', sans-serif;
    font-weight: 600;
    position: relative;
    display: inline-block;
  }
  
  .footer-contact h4:after, .footer-links h4:after {
    background: #d4af37;
    width: 40px;
    left: 50%;
    transform: translateX(-50%);
  }
  
  .contact-container {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }
  
  .contact-group {
    background: rgba(250, 249, 247, 0.05);
    padding: 15px;
    border-radius: 8px;
    border: 1px solid rgba(212, 175, 55, 0.2);
    flex: 0 0 100%;
  }
  
  .contact-group h5 {
    font-size: 14px;
    margin-bottom: 10px;
    color: #faf9f7;
    font-family: 'Microsoft YaHei', '', sans-serif;
    font-weight: 600;
  }
  
  .contact-group p {
    font-size: 12px;
    margin-bottom: 5px;
    color: rgba(250, 249, 247, 0.8);
    font-family: 'Microsoft YaHei', '', sans-serif;
  }
  
  .footer-links ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;
  }
  
  .footer-links ul li a {
    font-size: 13px;
    color: rgba(250, 249, 247, 0.8);
    font-family: 'Microsoft YaHei', '', sans-serif;
    padding: 8px 15px;
    background: rgba(212, 175, 55, 0.1);
    border-radius: 20px;
    border: 1px solid rgba(212, 175, 55, 0.3);
    transition: all 0.3s ease;
    text-decoration: none;
  }
  
  .footer-links ul li a:hover {
    background: #d4af37;
    color: #2c2c2c;
    transform: translateY(-2px);
    padding-left: 15px;
  }
  
  .social-links {
    justify-content: center;
    margin-top: 15px;
    gap: 15px;
  }
  
  .social-link {
    width: 40px;
    height: 40px;
    background: rgba(212, 175, 55, 0.1);
    border: 2px solid rgba(212, 175, 55, 0.3);
    border-radius: 8px;
  }
  
  .social-link:hover {
    background: #d4af37;
    border-color: #d4af37;
  }
  
  .social-icon {
    width: 24px;
    height: 24px;
  }
  
  .footer-bottom {
    padding: 15px;
    background: rgba(44, 44, 44, 0.8);
    border-top: 1px solid rgba(212, 175, 55, 0.3);
    text-align: center;
  }
  
  .copy {
    font-size: 11px;
    color: rgba(250, 249, 247, 0.7);
    font-family: 'Microsoft YaHei', '', sans-serif;
    margin: 0;
  }
}

@media (max-width: 400px) {
  .footer-brand .logo {
    width: 48px;
    height: 24px;
  }
  .qr-code {
    width: 56px;
    height: 56px;
  }
  .footer-content {
    padding: 10px 1% 4px;
  }
  .footer-brand .title, .footer-contact h4, .footer-links h4 {
    font-size: 12px;
  }
  .contact-group h5 {
    font-size: 11px;
  }
  .footer-contact p, .footer-links ul li a, .copy {
    font-size: 10px;
  }
}
.footer-bottom {
  padding: 20px 10%;
  background: #1a2530;
  text-align: center;
  display: flex;
  justify-content: center; /* flex */
  align-items: center;
}

.copy {
  margin: 0;
  font-size: 14px;
  color: #95a5a6;
  display: inline-block; /*  */
}
</style>




