<template>
  <div id="GoTop" @click="GoTop()" v-show="showButton" class="animated gotop-container">
    <span class="gotop-text"></span>
  </div>
</template>
<script>
export default {
  name: "GoTop",
  data() {
    return {
      showButton: false
    };
  },
  methods: {
    GoTop() {
      (function smoothscroll() {
        var currentScroll =
          document.documentElement.scrollTop || document.body.scrollTop;
        if (currentScroll > 0) {
          window.requestAnimationFrame(smoothscroll);
          window.scrollTo(0, currentScroll - currentScroll / 8); // 
        }
      })();
    },
    
    // /
    handleScroll() {
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      this.showButton = scrollTop > 300; // 300px
    }
  },
  mounted() {
    // 
    window.addEventListener('scroll', this.handleScroll);
  },
  beforeDestroy() {
    // 
    window.removeEventListener('scroll', this.handleScroll);
  }
};
</script>
<style scoped>
#GoTop.gotop-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  right: 24px;
  bottom: 24px;
  z-index: 99999999;
  cursor: pointer;
  background: #fff;
  color: #3498db;
  border-radius: 32px;
  box-shadow: 0 2px 12px rgba(52,152,219,0.18);
  transition: box-shadow 0.3s cubic-bezier(.4,0,.2,1), background 0.3s, color 0.3s;
  padding: 0 18px;
  min-width: 48px;
  height: 48px;
  font-size: 15px;
  font-weight: 500;
  overflow: visible;
}
#GoTop.gotop-container:hover {
  background: #3498db;
  color: #fff;
  box-shadow: 0 8px 24px rgba(41,128,185,0.25);
  transform: translateY(-6px) scale(1.08);
}
/*  .gotop-icon-bg  */
.gotop-text {
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 0.5px;
  white-space: nowrap;
  opacity: 1;
  transition: opacity 0.3s;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .gotop-text {
    display: none;
  }
  #GoTop.gotop-container {
    padding: 0 8px;
    min-width: 40px;
    height: 40px;
  }
}
@media (max-width: 575px) {
  #GoTop.gotop-container {
    right: 10px;
    bottom: 10px;
    min-width: 32px;
    height: 32px;
    padding: 0 6px;
    font-size: 12px;
    border-radius: 18px;
  }
  .gotop-text {
    display: none;
  }
}
@media (max-width: 400px) {
  #GoTop.gotop-container {
    right: 4px;
    bottom: 4px;
    min-width: 24px;
    height: 24px;
    padding: 0 2px;
    font-size: 10px;
    border-radius: 12px;
  }
}
.animated {
  animation: bounce 0.5s;
}
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-8px);
  }
  60% {
    transform: translateY(-4px);
  }
}
</style>


