<template>
  <div id="app">
    <Header></Header>
    <router-view/>
    <Footer></Footer>
    <GoTop></GoTop>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
/*  */
html, body {
  overflow-x: hidden;
  width: 100%;
  min-width: 0;
  margin: 0;
  padding: 0;
}

#app {
  width: 100%;
  min-width: 0;
  overflow-x: hidden;
}

/*  */
.container, .container-fluid {
  min-width: 0;
  width: 100%;
  box-sizing: border-box;
}

/*  */
img {
  max-width: 100%;
  height: auto;
}

/*  */
@media (max-width: 767px) {
  .row {
    margin-left: -10px;
    margin-right: -10px;
  }
  
  [class*="col-"] {
    padding-left: 10px;
    padding-right: 10px;
  }
}
</style>



