import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '*',
      redirect: '/'
    }, {
      path: '/',
      name: 'PageView',
      component: resolve => require(['@/view/PageView'], resolve),
      children: [
        {
          path: '/',
          redirect: '/home'
        }, {
          path: '/home',
          name: 'home',
          component: resolve => require(['@/view/HomePage'], resolve),
          meta: {
            title: ''
          }
        }, {
          path: '/bussiness',
          name: 'bussiness',
          component: resolve => require(['@/view/Software'], resolve),
          meta: {
            title: ''
          },
          children: [
            {
              path: '/bussiness',
              redirect: '/bussiness/zhimingzhePlatform'
            },
            {
              path: '/bussiness/zhimingzhePlatform',
              name: 'zhimingzhePlatform',
              component: resolve => require(['@/view/zhimingzhePlatform'], resolve),
              meta: {
                title: ''
              }
            },
            {
              path: '/bussiness/zhimingzheApp',
              name: 'zhimingzheApp',
              component: resolve => require(['@/view/zhimingzheApp'], resolve),
              meta: {
                title: ''
              }
            }
          ]
        }, 
        {
          path: '/service',
          name: 'service',
          component: resolve => require(['@/view/Service'], resolve),
          meta: {
            title: ''
          }
        }, {
          path: '/zhimingzheBigPlatform',
          name: 'ZhimingzheBigPlatform',
          component: () => import('@/view/zhimingzheBigPlatform/index')
        }, {
          path: '/newsinformation',
          name: 'newsinformation',
          component: resolve => require(['@/view/NewsInformation'], resolve),
          meta: {
            title: ''
          }
        }, {
          path: '/companyintroduction',
          name: 'companyintroduction',
          component: resolve => require(['@/view/CompanyIntroduction'], resolve),
          meta: {
            title: ''
          }
        }, {
          path: '/jobchance',
          name: 'jobchance',
          component: resolve => require(['@/view/JobChance'], resolve),
          meta: {
            title: ''
          }
        }, {
          path: '/contactus',
          name: 'contactus',
          component: resolve => require(['@/view/ContactUs'], resolve),
          meta: {
            title: ''
          }
        },
        {
          path: '/servicedetail',
          name: 'servicedetail',
          component: resolve => require(['@/view/ServiceDetail'],resolve),
          meta: {
            title: ''
          }
        }
      ,{
        path: '/expertTeamDetail/:teamType',
        name: 'expertTeamDetail',
        component: resolve => require(['@/view/ExpertTeamDetail'], resolve),
        meta: {
          title: ''
        }
      }
    ]
  }
  ],
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})

// 
const originalPush = Router.prototype.push
Router.prototype.push = function push(location, onResolve, onReject) {
  if (onResolve || onReject) {
    return originalPush.call(this, location, onResolve, onReject)
  }
  return originalPush.call(this, location).catch(err => {
    if (err.name !== 'NavigationDuplicated') {
      console.error('Navigation error:', err)
    }
  })
}

const originalReplace = Router.prototype.replace
Router.prototype.replace = function replace(location, onResolve, onReject) {
  if (onResolve || onReject) {
    return originalReplace.call(this, location, onResolve, onReject)
  }
  return originalReplace.call(this, location).catch(err => {
    if (err.name !== 'NavigationDuplicated') {
      console.error('Navigation error:', err)
    }
  })
}

export default router



